export default{
  //方法
  updateUserName(state, payload) {
    state.globalUserName = payload
  },
  updateRegister(state, payload) {
    state.registerStatus = payload
  },
  updateSocket(state, payload) {
    state.stompClient = payload
  },
  addMessage(state, payload) {
    state.messages.push(payload)
  },
  addNewMessager(state, payload){
    state.listenMembers.push(payload)
    console.log(state.listenMembers)
  },
  subNewMessager(state, payload){
    let index = state.listenMembers.findIndex(item => item===payload)
    state.messages.splice(index, 1)
    console.log(state.listenMembers)
  }
}



